	var _self;
    export default {
        data() {
            return {
                msgContents: ["界面显示错乱", "启动缓慢，卡出翔了", "UI无法直视，丑哭了", "偶发性崩溃"],
                stars: [1, 2, 3, 4, 5],
                imageList: [],
                sendDate: {
                    score: 0,
                    content: "",
                    contact: ""
                }
            }
        },
        onLoad() {
			_self = this;
			let deviceInfo = {
			    appid: '__UNI__B3AB472',
			    imei: "", //设备标识
			    p: "", //平台类型，i表示iOS平台，a表示Android平台。
			    md: "", //设备型号
			    app_version: "",
			    plus_version: "", //基座版本号
			    os: "",
			    net: ""
			}
			uni.getSystemInfo({
				success: function (res) {
					deviceInfo.p = res.platform;
					deviceInfo.md = res.model;
					deviceInfo.app_version = res.version;
					deviceInfo.os = res.system;
					_self.sendDate = Object.assign(deviceInfo, _self.sendDate);
				}
			});
            
            
        },
        methods: {
            close(e){
                this.imageList.splice(e,1);
            },
            chooseMsg() { //快速输入
                uni.showActionSheet({
                    itemList: this.msgContents,
                    success: (res) => {
                        this.sendDate.content = this.msgContents[res.tapIndex];
                    }
                })
            },
            chooseImg() { //选择图片
                uni.chooseImage({
                    sourceType: ["camera", "album"],
                    sizeType: "compressed",
                    count: 8 - this.imageList.length,
                    success: (res) => {
                        this.imageList = this.imageList.concat(res.tempFilePaths);
                    }
                })
            },
            chooseStar(e) { //点击评星
                this.sendDate.score = e;
            },
            previewImage() { //预览图片
                uni.previewImage({
                    urls: this.imageList
                });
            },
            send() { //发送反馈
                uni.showToast({
                    title: "反馈成功!"
                });
                this.imageList = [];
                this.sendDate = {
                    score: 0,
                    content: "",
                    contact: ""
                },
				setTimeout(()=>{
					uni.switchTab({
						url: '/pages/user/index'
					});
				},1500);
				
            }
        }
    }
